import * as React from 'react';
import { Plugin, Inline, RenderNodeProps } from '@ali/4ever-cangjie';
import Image from './components/image';
import { ImagePluginConfig } from './types';

export default function createRenderNode(config: ImagePluginConfig, state) {
  return {
    image: (props: RenderNodeProps<Inline>, controller) => {
      const { children, parent, node, ...rest } = props;
      const { id } = node.data;
      const { key } = node;
      const pluginState = state[id] || state[key] || {};
      return (
        // @ts-ignore
        <Image
          {...rest}
          controller={controller}
          pluginState={pluginState}
          imageConfig={config || {}}
          node={node}
          parent={parent}
        >
          {/* @ts-ignore */}
          {children()}
        </Image>
      );
    },
  };
}
